<template>
   <v-app>
      <v-main class="grey lighten-3">
         <v-container>
            <v-row>
               <v-col cols=2 align="center">
                  <v-btn color="green darken-1" @click="loadClips">Load Clips</v-btn>
               </v-col>
               <v-col cols="3">
                  <v-select dense :items="timeOptions" v-model="currentTimeOptions" label="Select time-area" outlined></v-select>               
               </v-col>
               <v-col cols="3" v-show="allClips.length > 1" style="color: #43A047;"><h3>{{allClips.length}} clips are currently loaded!)</h3></v-col>
               <v-col cols="3" v-if="allClips.length == 1" style="color: #43A047;"><h3>{{allClips.length}} clip is currently loaded!</h3></v-col>
               <v-col cols="3" v-if="allClips.length == 0" style="color: #E53935;"><h3>No clips loaded!</h3></v-col>
            </v-row>
            <v-row>
               <v-col>
                  <v-simple-table>
                     <template v-slot:default>
                        <thead>
                        <tr>
                           <th class="text-left">Title</th>
                           <th class="text-left">Clip Autor</th>
                           <th class="text-left">Views</th>
                           <th class="text-left">Game</th>
                           <th class="text-left">Date</th>
                           <th class="text-left">Votes</th>
                           <th class="text-left">Voted</th>

                        </tr>
                        </thead>
                        <tbody>
                        <tr v-for="(item,a) in allClips" :key="a" style="cursor: pointer" @click="loadUrl(allClips.indexOf(item))">
                           <td>{{ item.name }}</td>
                           <td>{{ item.author }}</td>
                           <td>{{ item.views }}</td>
                           <td>{{ item.game }}</td>
                           <td>{{ item.date}}</td>
                           <td>{{ item.votes}}</td>
                           <td>{{ item.voted}}</td>
                        </tr>
                        </tbody>
                     </template>
                  </v-simple-table> 
               </v-col>
            </v-row>
         </v-container>
      </v-main>
   </v-app>
</template>

<script>
   export default {
     data: () => ({
        timeOptions: ["Day","Week","Month"],
        currentTimeOptions: "Month"
     }),
     methods: {
        loadClips: function(){


         var xmlHttp = new XMLHttpRequest();
         xmlHttp.open( "GET", `https://api.twitch.tv/kraken/clips/top?channel=${this.settings.streamername}&limit=100&period=` + this.currentTimeOptions.toLowerCase(), false ); 
         xmlHttp.setRequestHeader("Accept","application/vnd.twitchtv.v5+json")
         xmlHttp.setRequestHeader("Client-ID","19e3dcu3gcraaro165ldrodkroql1e")
         xmlHttp.send( null );

         var response = xmlHttp.responseText;
         var parsedResponse = JSON.parse(response);
         var clipArray = parsedResponse.clips;
         for(var i=0; i < clipArray.length;i++){
            var currentClip=clipArray[i];
             this.allClips.push({name: currentClip.title,
                              slug: currentClip.slug,
                              url: currentClip.url,
                              author: currentClip.curator.name,
                              views: currentClip.views,
                              game: currentClip.game, 
                              date: currentClip.created_at.split("T")[0],
                              votes: 0,
                              voted: false})
         }
        },
        loadUrl: function(index){
           window.open(this.allClips[index].url,'_blank');
        }
     },
      props: [
         'allClips',
         'settings'
      ]
   }
</script>